@import url('https://fonts.googleapis.com/css2?family=Open+Sans&display=swap');

*
{
  padding: 0; margin: 0;
  box-sizing: border-box;
  font-family: "Open Sans";
}

body
{
  width: 100vw; height: 100vh;
  background: burlywood;
}

div
{
  /* Temporary centering */
  width: 100vw;
  text-align: center;
  position: absolute;
  transform: translateY(-50%);
  top: 50%;
}
